html {
  font-size: 0.26666667vw;
}
.errorMsg {
  position: absolute;
  background-color: #e60d0d;
  width: 344rem;
  font-size: 20px;
  color: white;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  top: 285rem;
}
.am-tab-bar {
  position: fixed;
  z-index: 99;
  bottom: 0;
  width: 100%;
  height: inherit;
  /* inherit为从父元素继承 height 属性的值。从字面上理解和100%并无不同，但是因为这两个是计算值，在某些属性变化后就有所不同了 */
}
.top-fixed-bar {
  position: fixed;
  z-index: 99;
  top: 0;
  width: 100%;
}
